            <div class="total_col">
                <strong>Campaign Total Sent <i aria-hidden="true" class="fa fa-caret-down"></i></strong>
                <span class="count" v-if="stats.campaigns">{{stats.campaigns.sent.total}}</span>
                <ul class="total_drop">
                    <li>
                        <a href="#">
                            <span v-if="stats.campaigns">{{stats.campaigns.sent.email}}</span>
                            <em><i aria-hidden="true" class="fa fa-envelope"></i> Email</em>
                        </a>
                    </li>
                    <li>
                        <a href="#">
                            <span v-if="stats.campaigns">{{stats.campaigns.sent.push}}</span>
                            <em><i aria-hidden="true" class="fa fa-paper-plane"></i> Push</em>
                        </a>
                    </li>
                    <li>
                        <a href="#">
                            <span v-if="stats.campaigns">{{stats.campaigns.sent.inapp}}</span>
                            <em><i aria-hidden="true" class="fa fa-archive"></i> In App</em>
                        </a>
                    </li>
                </ul>
            </div>
            <div class="total_col">
                <strong>Campaign Total Fail <i aria-hidden="true" class="fa fa-caret-down"></i></strong>
                <span class="count" v-if="stats.campaigns">{{stats.campaigns.failed.total}}</span>
                <ul class="total_drop">
                    <li>
                        <a href="#">
                            <span v-if="stats.campaigns">{{stats.campaigns.failed.email}}</span>
                            <em><i aria-hidden="true" class="fa fa-envelope"></i> Email</em>
                        </a>
                    </li>
                    <li>
                        <a href="#">
                            <span v-if="stats.campaigns">{{stats.campaigns.failed.push}}</span>
                            <em><i aria-hidden="true" class="fa fa-paper-plane"></i> Push</em>
                        </a>
                    </li>
                    <li>
                        <a href="#">
                            <span v-if="stats.campaigns">{{stats.campaigns.failed.inapp}}</span>
                            <em><i aria-hidden="true" class="fa fa-archive"></i> In App</em>
                        </a>
                    </li>
                </ul>
            </div>
            <div class="total_col">
                <strong>Campaign In Queue <i aria-hidden="true" class="fa fa-caret-down"></i></strong>
                <span class="count" v-if="stats.campaigns">{{stats.campaigns.queued.total}}</span>
                <ul class="total_drop">
                    <li>
                        <a href="#">
                            <span v-if="stats.campaigns">{{stats.campaigns.queued.email}}</span>
                            <em><i aria-hidden="true" class="fa fa-envelope"></i> Email</em>
                        </a>
                    </li>
                    <li>
                        <a href="#">
                            <span v-if="stats.campaigns">{{stats.campaigns.queued.push}}</span>
                            <em><i aria-hidden="true" class="fa fa-paper-plane"></i> Push</em>
                        </a>
                    </li>
                    <li>
                        <a href="#">
                            <span v-if="stats.campaigns">{{stats.campaigns.queued.inapp}}</span>
                            <em><i aria-hidden="true" class="fa fa-archive"></i> In App</em>
                        </a>
                    </li>
                </ul>
            </div>
            <div class="total_col">
                <strong>Newsfeed Clicks <i aria-hidden="true" class="fa fa-caret-down"></i></strong>
                <span class="count" v-if="stats.newsfeed">{{stats.newsfeed.clicks.total}}</span>
                <ul class="total_drop">
                    <li>
                        <a href="#">
                            <span v-if="stats.newsfeed">{{stats.newsfeed.clicks.ios}}</span>
                            <em><i aria-hidden="true" class="fab fa-windows"></i> IOS</em>
                        </a>
                    </li>
                    <li>
                        <a href="#">
                            <span v-if="stats.newsfeed">{{stats.newsfeed.clicks.android}}</span>
                            <em><i aria-hidden="true" class="fab fa-android"></i> Android</em>
                        </a>
                    </li>
                    <li>
                        <a href="#">
                            <span v-if="stats.newsfeed">{{stats.newsfeed.clicks.web}}</span>
                            <em><i aria-hidden="true" class="fab fa-apple"></i> Web</em>
                        </a>
                    </li>
                </ul>
            </div>
            <div class="total_col">
                <strong>Newsfeed Views <i aria-hidden="true" class="fa fa-caret-down"></i></strong>
                <span class="count" v-if="stats.newsfeed">{{stats.newsfeed.views.total}}</span>
                <ul class="total_drop">
                    <li>
                        <a href="#">
                            <span v-if="stats.newsfeed">{{stats.newsfeed.views.ios}}</span>
                            <em><i aria-hidden="true" class="fa fa-envelope"></i> IOS</em>
                        </a>
                    </li>
                    <li>
                        <a href="#">
                            <span v-if="stats.newsfeed">{{stats.newsfeed.views.android}}</span>
                            <em><i aria-hidden="true" class="fa fa-paper-plane"></i> Android</em>
                        </a>
                    </li>
                    <li>
                        <a href="#">
                            <span v-if="stats.newsfeed">{{stats.newsfeed.views.web}}</span>
                            <em><i aria-hidden="true" class="fa fa-archive"></i> Web</em>
                        </a>
                    </li>
                </ul>
            </div>
            <div class="total_col">
                <strong><a href="#"><i aria-hidden="true" class="fa fa-plus"></i> Quick Actions</a></strong>
                <ul class="total_drop">
                    <li>
                        <a href="#">
                            <em><i aria-hidden="true" class="fa fa-plus"></i> Create Email</em>
                        </a>
                    </li>
                    <li>
                        <a href="#">
                            <em><i aria-hidden="true" class="fa fa-rocket"></i> Create in App</em>
                        </a>
                    </li>
                    <li>
                        <a href="#">
                            <em><i aria-hidden="true" class="fa fa-plus"></i> Create Push</em>
                        </a>
                    </li>
                    <li>
                        <a href="#">
                            <em><i aria-hidden="true" class="fa fa-rss"></i> Newsfeed</em>
                        </a>
                    </li>
                </ul>
            </div>
        </div>
        <div class="padding_content stats_div">
            <div class="homepage_charts">
                <article class="post">
                    <div class="user_state_cols">
                        <div class="state_col">
                            <div class="state_header">
                                <div class="align_right">
                                    <select v-model="type" v-on:change="this.onType">
                                        <option value="weekly">Weekly</option>
                                        <option value="monthly">Monthly</option>
                                        <option value="yearly">Yearly</option>
                                    </select>
                                    <ul class="state_list">
                                        <li>
                                            <em class="color_indicate send"></em>
                                            <em>Send</em>
                                        </li>
                                        <li>
                                            <em class="color_indicate failed"></em>
                                            <em>Failed</em>
                                        </li>
                                        <li>
                                            <em class="color_indicate queue"></em>
                                            <em>In Queue</em>
                                        </li>
                                    </ul>
                                </div>
                                <h2>Email Campaign</h2>
                            </div>
                            <div id="chart1" style="height: 300px; width: 100%;"></div>
                        </div>
                        <div class="state_col">
                            <div class="state_header">
                                <div class="align_right">
                                    <select v-model="conversationType" v-on:change="this.onConversationType">
                                        <option value="weekly">Weekly</option>
                                        <option value="monthly">Monthly</option>
                                        <option value="yearly">Yearly</option>
                                    </select>
                                    <ul class="state_list">
                                        <li>
                                            <em class="color_indicate send"></em>
                                            <em>IOS</em>
                                        </li>
                                        <li>
                                            <em class="color_indicate failed"></em>
                                            <em>Android</em>
                                        </li>
                                        <li>
                                            <em class="color_indicate queue"></em>
                                            <em>Web</em>
                                        </li>
                                    </ul>
                                </div>
                                <h2>Conversion</h2>
                            </div>
                            <div id="chart2" style="height: 300px; width: 100%;"></div>
                        </div>
                    </div>
                </article>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "dashboardStats",
        data() {
            return {
                stats: [],
                emailData: [],
                conversationData: [],
                type: 'weekly',
                conversationType: 'weekly'
            }
        },
        mounted() {
            this.listenForDashboardEvent();
            this.getStats();
        },
        updated() {
            $('.total_col').hover(function () {
                $(this).find('.total_drop').toggleClass('active');
            });
        },
        beforeDestroy() {
            this.bus.$off("callDashboardStatsApis");
        },
        methods: {
            listenForDashboardEvent() {
                this.bus.$on("callDashboardStatsApis", () => {
                    this.getStats();
                });
            },
            getStats() {
                this.getDashboardStats();
                this.getChartRecords(this.type);
                this.getConversationChartRecords(this.conversationType);
            },
            onType(e) {
                this.getChartRecords(e.target.value)
            },
            onConversationType(e) {
                this.getConversationChartRecords(e.target.value)
            },
            getDashboardStats() {
                var payload = {
                    resource: "dashboard/stats",
                    action: "get",
                    data: {}
                };
                this.api("post",
                    this.constants.getUrl("backendApiUrl"),
                    payload,
                    this.authHeaders
                ).then((response) => {
                    this.stats = response.data.data
                }).catch((error) => {
                    console.log(error.response.data)
                });
            },
            getChartRecords(type) {
                var payload = {
                    resource: "dashboard/email/campaign",
                    action: "get",
                    data: {type: type}
                };
                this.api("post",
                    this.constants.getUrl("backendApiUrl"),
                    payload,
                    this.authHeaders
                ).then((response) => {
                    console.log(response.data.data);
                    this.emailData = response.data.data;
                    this.emailCampaignChart();
                }).catch((error) => {
                    console.log(error.response.data)
                });
            },
            getConversationChartRecords(type) {
                var payload = {
                    resource: "dashboard/conversation/campaign",
                    action: "get",
                    data: {type: type}
                };
                this.api("post",
                    this.constants.getUrl("backendApiUrl"),
                    payload,
                    this.authHeaders
                ).then((response) => {
                    console.log("conversation chart", response.data.data);
                    this.conversationData = response.data.data;
                    this.conversationChart();
                }).catch((error) => {
                    console.log(error.response.data)
                });
            },
            emailCampaignChart() {
                var chart = new CanvasJS.Chart("chart1", {
                    animationEnabled: true,
                    title: {
                        text: ""
                    },
                    axisY: {
                        title: ""
                    },
                    legend: {
                        cursor: "pointer",
                        itemclick: this.toggleDataSeries
                    },
                    toolTip: {
                        shared: true,
                        content: this.toolTipFormatter
                    },
                    data: this.emailData
                });
                chart.render();
            },
            toolTipFormatter(e) {
                var str = "";
                var total = 0;
                var str3;
                var str2;
                for (var i = 0; i < e.entries.length; i++) {
                    var str1 = "<span style= \"color:" + e.entries[i].dataSeries.color + "\">" + e.entries[i].dataSeries.name + "</span>: <strong>" + e.entries[i].dataPoint.y + "</strong> <br/>";
                    total = e.entries[i].dataPoint.y + total;
                    str = str.concat(str1);
                }
                str2 = "<strong>" + e.entries[0].dataPoint.label + "</strong> <br/>";
                str3 = "<span style = \"color:Tomato\">Total: </span><strong>" + total + "</strong><br/>";
                return (str2.concat(str)).concat(str3);
            },
            toggleDataSeries(e) {
                if (typeof (e.dataSeries.visible) === "undefined" || e.dataSeries.visible) {
                    e.dataSeries.visible = false;
                } else {
                    e.dataSeries.visible = true;
                }
                chart.render();
            },
            conversationChart() {
                var chart = new CanvasJS.Chart("chart2", {
                    animationEnabled: true,
                    title: {
                        text: ""
                    },
                    axisY: {
                        title: ""
                    },
                    legend: {
                        cursor: "pointer",
                        itemclick: this.toggleDataSeries
                    },
                    toolTip: {
                        shared: true,
                        content: this.toolTipFormatter
                    },
                    data: this.conversationData
                });
                chart.render();
            }
        }
    }
</script>

<style>

</style>


