<template>
    <div id="wrapper">
        <div class="login_layer">
            <div class="d-table">
                <div class="v-middle">
                    <form v-bind:class="classActive == true ? 'login_form active': 'login_form'">
                        <fieldset>
                            <h2>
                                <div class="icon"><i class="fas fa-key"></i></div>
                                <span>Reset Password</span>
                            </h2>
                            <div class="fields">
                                <div class="row">
                                    <div class="input_holder">
                                        <i class="fas fa-user"></i>
                                        <input type="email" placeholder="Email" v-model="user.email">
                                    </div>
                                </div>
                                <div class="row">
                                    <input type="button" value="SEND RESET LINK" v-on:click="forgotPassword">
                                </div>
                                <div class="row border">
                                    <div class="col fluid">
                                        <p>Back to
                                            <router-link to="/login"> Login Page?</router-link>
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </fieldset>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "forgot-password",
        data() {
            return {
                user: {
                    email: "",
                },
                classActive: false,
            };
        },
        mounted() {
            this.classActive = true;
        },
        methods: {
            forgotPassword() {
                alert("in forgotPassword");
            }
        }
    }
</script>

<style>

</style>


