<template>
    <header id="header">

        <router-link to="/dashboard/dashboard-stats" class="logo">
            <img v-if="user.logo" :src="user.logo" alt="Hermis">
            <img v-else src="../../../assets/images/logo.png" alt="Hermis">
        </router-link>

        <span class="slogan">
                <a href="#" class="nav_opener"><img src="../../../assets/images/nav_opener.png" alt="#"></a>
                <em>{{user.name}}</em>
            </span>
        <ul class="list_none inline_list user_links">
            <li class="sec_hidden"><a class="anchor_pointer"><i class="fas fa-folder-open"></i></a></li>
            <li class="sec_hidden"><a class="anchor_pointer"><i class="far fa-chart-bar"></i></a></li>
            <li class="sec_hidden"><a class="anchor_pointer"><i class="far fa-envelope"></i></a></li>
            <li><a class="anchor_pointer"><i class="far fa-bell"></i></a>
            </li>
        </ul>
        <div class="float_right">
            <input type="text" class="header_search sec_hidden" placeholder="Search Anything Here...">
            <div class="logout_area">
                <a href="#" class="drop_opener"><i class="fa fa-ellipsis-v" aria-hidden="true"></i></a>
                <ul class="list_none profile_dropdown">
                    <!--<li><a href="#"><i class="fas fa-user-edit"></i> Edit Profile </a></li>-->
                    <li>
                        <router-link :to="'/dashboard/company-edit/'+ user.id">
                            <i class="fas fa-user-edit"></i> Edit Profile
                        </router-link>
                    </li>
                    <li><a style="cursor: pointer" v-on:click="logOut"><i class="fas fa-sign-out-alt"></i> Log Out </a>
                    </li>

                    <!--<li><a href="#"><i class="fas fa-sign-out-alt"></i> Log Out </a></li>-->
                </ul>
            </div>
        </div>
    </header>
</template>

<script>
    export default {
        data() {
            return {
                user: []
            }
        },
        mounted() {
            var payload = {
                resource: "user",
                action: "get",
                data: {}
            };
            this.api("post",
                this.constants.getUrl("backendApiUrl"),
                payload,
                this.authHeaders
            ).then((response) => {
                this.user = response.data
            }).catch((error) => {
            });
        },
        methods: {
            logOut() {
                this.api("post",
                    this.constants.getUrl("logOut"),
                    {},
                    this.authHeaders
                ).then((response) => {
                    this.removeToken();
                    this.$router.go("/login");
                }).catch((error) => {
                    this.removeToken();
                    this.$router.go("/login");
                });

            }
        }
    }
</script>

<style>

</style>


