import axios from 'axios';
import store from "../store/store";
import router from "../router/routes";


const api = function (type, url, data, headers = {}, loader = false) {
    return new Promise(function (resolve, reject) {

        //axios.defaults.headers.post['Content-Type'] ='application/json;charset=utf-8';
        //axios.defaults.headers.post['Accept'] = 'application/json, text/plain, *!/!*';
        //axios.defaults.headers.post['Access-Control-Allow-Methods'] = 'GET, PUT, POST, DELETE, OPTIONS';
        //axios.defaults.headers.post['Access-Control-Allow-Origin'] = '*';
        //axios.defaults.timeout = 1000;
        //axios.defaults.withCredentials = false;
        //axios.defaults.proxyHeaders =  false;
        if (loader) {
            $(".ajax_loader").show();
        }
        axios[type](
            url,
            data,
            headers,
        ).then((response) => {
            $(".ajax_loader").hide();
            resolve(response);
        }).catch((...error) => {
            if (error[0].response.data.meta.code == 401) {
                store.dispatch('removeStoreTokenAction');
                router.go("/login");
            }
            $(".ajax_loader").hide();
            reject(error);
        });

    });
};

export default api;


