<template>
    <div id="wrapper">
        <div class="login_layer">
            <div class="d-table">
                <div class="v-middle">

                    <form v-bind:class="classActive == true ? 'login_form active': 'login_form'"
                          v-on:submit.prevent="login">
                        <fieldset>
                            <div class="hermis_logo">
                                <img src="../../assets/images/hermisLogo.png">
                            </div>
                            <h2>
                                <div class="icon"><i class="fas fa-lock"></i></div>
                                <span>Member Login</span>
                            </h2>
                            <div class="fields">
                                <div class="row">
                                    <div class="input_holder">
                                        <i class="fas fa-user"></i>
                                        <input type="email" placeholder="Email" v-model="user.email">
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="input_holder">
                                        <i class="fas fa-unlock-alt"></i>
                                        <input type="password" placeholder="Password" v-model="user.password">
                                    </div>
                                </div>
                                <div class="row b_margin">
                                    <div class="col">
                                        <router-link to="/forgot-password">Forgot Password?</router-link>
                                    </div>
                                </div>
                                <div class="row">
                                    <input type="submit" value="Login" v-on:click="login">
                                </div>
                            </div>
                        </fieldset>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Login",
        data() {
            return {
                user: {
                    email: "company@engagement.com",
                    password: "123456"
                },
                classActive: false,
            };
        },
        mounted() {
            this.classActive = true;
        },
        methods: {
            login() {
                this.api("post",
                    this.constants.getUrl("login"),
                    this.user
                ).then((response) => {
                    if (response.data.meta.code == 200) {
                        this.storeToken(response.data.data.token);
                        this.$router.go("/dashboard/dashboard-stats");
                    }
                }).catch((error) => {
                    this.$swal('Failed', 'These credentials do not match our records.', 'error');
                });
            }
        }
    }
</script>

<style scoped>
    .hermis_logo {
        text-align: center;
        padding: 8px 0px;
    }
</style>


