<template>
    <div class="page_content_holder">
        <div class="padding_content">
            <div class="white_boxed_content">
                <h1>Edit Company</h1>
                <div class="new_padding_div">
                    <ul class="tabs stats_tabs list_none inline_list">
                        <li v-bind:class="[ activetab === 1 ? 'active' : '' ]" v-on:click="activetab=1"><a>General</a>
                        </li>
                        <li v-bind:class="[ activetab === 2 ? 'active' : '' ]" v-on:click="activetab=2"><a>Password</a>
                        </li>
                    </ul>
                    <div class="stats_tab_content" id="tab-content">
                        <div class="tab active" id="overview" v-if="activetab === 1">
                            <div class="state_cols">
                                <div class="row">
                                    <div class="col-sm-12">
                                        <div class="col-sm-2"></div>
                                        <div class="col-sm-8">
                                            <label>Name:</label>
                                            <input class="form-control" required type="text" v-model="companyObj.name"/>
                                        </div>
                                        <div class="col-sm-2"></div>
                                    </div>
                                </div>
                                <br/>
                                <div class="row">
                                    <div class="col-sm-12">
                                        <div class="col-sm-2"></div>
                                        <div class="col-sm-8">
                                            <label>Email:</label>
                                            <input class="form-control" disabled="disabled" type="text"
                                                   v-model="companyObj.email"/>
                                        </div>
                                        <div class="col-sm-2"></div>
                                    </div>
                                </div>
                                <br>
                                <div class="row">
                                    <div class="col-sm-12">
                                        <div class="col-sm-2"></div>
                                        <div class="col-sm-8">
                                            <label>Logo:</label>
                                            <!--<input type="file" class="form-control" id="file" ref="file"-->
                                            <!--v-on:change="handleFileUpload()"/>-->
                                            <!--<img width="100px" height="100px" v-bind:src="companyObj.logo"/>-->
                                            <a v-on:click="activateGalleryModal">
                                                <label class="bordered_label">
                                                    <img alt="#" class="app_logo" v-bind:src="companyObj.logo"
                                                         v-if="companyObj.logo" width="100px" height="100px"/>
                                                    <img alt="#" class="app_logo"
                                                         src="../../../../assets/images/app-image.png"
                                                         v-else width="100px" height="100px"/>
                                                </label>
                                            </a>
                                        </div>
                                        <div class="col-sm-2"></div>
                                    </div>
                                </div>
                                <br>
                                <div class="row">
                                    <div class="col-sm-12">
                                        <div class="col-sm-2"></div>
                                        <div class="col-sm-8">
                                            <label>Company key:</label>
                                            <input class="form-control" readonly type="text" v-model="companyObj.key"/>
                                        </div>
                                        <div class="col-sm-2"></div>
                                    </div>
                                </div>
                                <br>
                                <div class="row">
                                    <div class="col-sm-12">
                                        <div class="col-sm-2"></div>
                                        <div class="col-sm-8">
                                            <label>Status:</label>
                                            <select class="form-control" v-model="companyObj.is_active">
                                                <option value="">select status</option>
                                                <option value="1">Active</option>
                                                <option value="0">In Active</option>
                                            </select>
                                        </div>
                                        <div class="col-sm-2"></div>
                                    </div>
                                </div>
                                <br>
                                <div class="row text-center">
                                    <button class="btn btn-primary" v-on:click="updateCompany">Update</button>
                                </div>
                            </div>
                        </div>
                        <div class="tab" id="engagement" v-if="activetab === 2">
                            <div class="state_cols">
                                <div class="row">
                                    <div class="col-sm-12">
                                        <div class="col-sm-2"></div>
                                        <div class="col-sm-8">
                                            <label>Password:</label>
                                            <input class="form-control" required type="password"
                                                   v-model="companyObj.password"/>
                                            <p class="error" v-if="errors.password">{{ errors.password }}</p>
                                        </div>
                                        <div class="col-sm-2"></div>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-sm-12">
                                        <div class="col-sm-2"></div>
                                        <div class="col-sm-8">
                                            <label>Confirm Password:</label>
                                            <input class="form-control" required
                                                   type="password" v-model="companyObj.confirmPassword"/>
                                            <p class="error" v-if="errors.confirmPassword">{{ errors.confirmPassword
                                                }}</p>
                                        </div>
                                        <div class="col-sm-2"></div>
                                    </div>
                                </div>
                                <br>
                                <div class="row text-center">
                                    <button class="btn btn-primary" v-on:click="updatePassword">Update</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: "editCompany",
        mounted() {
            this.companyObj.id = this.$route.params.id;
            this.getCompanylist();
            this.listenForCustomEvents();
        },
        data() {
            return {
                activetab: 1,
                companyObj: {
                    id: '',
                    name: '',
                    email: '',
                    key: '',
                    logo: '',
                    is_active: '',
                    password: '',
                    confirmPassword: '',
                },
                errors: {
                    name: '',
                    logo: '',
                    is_active: '',
                    password: '',
                    confirmPassword: ''
                }
            }
        },
        methods: {
            getCompanylist() {
                this.api("post",
                    this.constants.getUrl("backendApiUrl"),
                    {
                        resource: "company/edit",
                        action: "get",
                        id: this.companyObj.id
                    },
                    this.authHeaders
                ).then((response) => {
                    this.companyObj.email = response.data.data.email;
                    this.companyObj.name = response.data.data.name;
                    this.companyObj.key = response.data.data.company_key;
                    this.companyObj.is_active = response.data.data.is_active;
                    this.companyObj.logo = response.data.data.logo;
                    // this.$swal('Deleted', 'You successfully deleted this file', 'success');
                    // this.dataTable.reloadProp = !this.dataTable.reloadProp;
                }).catch((error) => {
                    console.log('error', error);
                });
            },
            handleFileUpload() {
                console.log('imagepath', this.$route);
                console.log('file', this.$refs.file.files[0]);
                let formData = new FormData();
                formData.append('file', this.$refs.file.files[0]);
                console.log('data', formData);
                this.api("post",
                    this.constants.getUrl("fileuploadUrl"),
                    formData,
                    {
                        headers: {
                            'Content-Type': 'multipart/form-data'
                        }
                    }
                ).then((response) => {
                    if (response.data.status == 200) {
                        this.$swal({
                            position: 'top-end',
                            type: 'success',
                            title: response.data.message,
                            showConfirmButton: false,
                            timer: 1500
                        });

                        this.companyObj.logo = response.data.imagePath;
                        this.updateCompany()
                    } else {
                        this.$swal({
                            position: 'top-end',
                            type: 'error',
                            title: response.data.message,
                            showConfirmButton: false,
                            timer: 1500
                        });
                    }
                    console.log('response', response);
                }).catch((error) => {
                    console.log('error', error);
                })

            },
            updateCompany() {
                var name = this.companyObj.name;
                var is_active = this.companyObj.is_active;
                var logo = this.companyObj.logo;
                this.api("post",
                    this.constants.getUrl("backendApiUrl"),
                    {
                        resource: "company",
                        action: "update",
                        id: this.companyObj.id,
                        data: this.companyObj
                    },
                    this.authHeaders
                ).then((response) => {
                    console.log('update', response);
                    if (response.data.status == 200) {
                        this.$swal({
                            position: 'top-end',
                            type: 'success',
                            title: 'Company has been Updated',
                            showConfirmButton: false,
                            timer: 1500
                        })
                    } else {
                        this.$swal({
                            position: 'top-end',
                            type: 'success',
                            title: 'Failed',
                            showConfirmButton: false,
                            timer: 1500
                        })
                    }
                    setTimeout(function () {
                        window.location.reload();
                    }, 1000);
                    this.dataTable.reloadProp = !this.dataTable.reloadProp;


                }).catch((error) => {
                    console.log('error', error);
                })
            },
            updatePassword() {
                var password = this.companyObj.password;
                var confirmPassword = this.companyObj.confirmPassword;
                if (password == '') {
                    this.errors.password = 'Please add password';
                }
                if (confirmPassword == '') {
                    this.errors.confirmPassword = 'Please add  confirm password';
                }
                if (password != '' && confirmPassword != '') {

                    this.api("post",
                        this.constants.getUrl("backendApiUrl"),
                        {
                            resource: "company/updatePassword",
                            action: "update",
                            id: this.companyObj.id,
                            data: this.companyObj
                        },
                        this.authHeaders
                    ).then((response) => {
                        console.log('update', response);
                        if (response.data.status == 200) {
                            this.$swal({
                                position: 'top-end',
                                type: 'success',
                                title: 'Company has been Updated',
                                showConfirmButton: false,
                                timer: 1500
                            })
                        } else {
                            this.$swal({
                                position: 'top-end',
                                type: 'success',
                                title: 'Failed',
                                showConfirmButton: false,
                                timer: 1500
                            })
                        }
                        console.log('response', response);
                        this.dataTable.reloadProp = !this.dataTable.reloadProp;
                    }).catch((error) => {
                        console.log('error', error);
                    })
                }
            },
            activateGalleryModal() {
                this.bus.$emit('galleryModalOpen', false);
            },
            listenForCustomEvents() {
                this.bus.$on("galleryModalReturn", (url) => {
                    console.log('url',url);
                    this.companyObj.logo = url;
                });
            }
        },
        watch: {
            '$route.params.id'(currVal, oldVal) {
                this.companyObj.id = this.currVal;
                this.getCompanylist();
            }
        }
    }
</script>

<style>

</style>


