<template>
    <div class="page_content_holder">
        <div class="padding_content">
            <div class="white_boxed_content lists_page">
                <header class="sec_header">
                    <div class="align_left">
                        <strong>Company</strong>
                    </div>
                    <input type="search" id="table_search" name="table_search"
                           v-model="lookupobj.search"
                           v-on:change="companySearch"
                           placeholder="Search here...">
                    <div class="align_right">
                        <strong class="actions_droper">Actions <i class="fas fa-sort-down"></i></strong>
                        <ul class="actions_drop list_none">
                            <li><a v-on:click="addCompany">Add Company</a></li>
                        </ul>
                    </div>
                </header>
                <div id="add_company" class="modal fade" role="dialog">
                    <div class="modal-dialog">
                        <!-- Modal content-->
                        <div class="modal-content">
                            <div class="modal-header">
                                <button type="button" class="close" data-dismiss="modal">&times;</button>
                                <h4 class="modal-title">Add Company</h4>
                            </div>
                            <div class="modal-body">
                                <div class="modal_row">
                                    <div class="modal_col">
                                        <h4 class="modal-title">Name:</h4>
                                        <input type="text" v-model="companyParamObj.name">
                                        <!--v-on:keyup="codeRegularExpression">-->
                                        <input type="hidden" id="lookupid" v-model="companyParamObj.companyid">
                                        <p class="error" v-if="errors.name">{{ errors.name }}</p>
                                    </div>
                                    <div class="modal_col">
                                        <h4 class="modal-title">Email:</h4>
                                        <input type="email" v-model="companyParamObj.email">
                                        <p class="error" v-if="errors.email">{{ errors.email }}</p>
                                    </div>
                                </div>
                                <div class="modal_row">
                                    <div class="modal_col">
                                        <h4 class="modal-title">Password</h4>
                                        <input type="password" v-model="companyParamObj.password">
                                        <p class="error" v-if="errors.password">{{ errors.password }}</p>
                                    </div>
                                    <div class="modal_col">
                                        <h4 class="modal-title">Confirm Password</h4>
                                        <input type="password" v-model="companyParamObj.confirmPassword">
                                        <p class="error" v-if="errors.confirmPassword">{{ errors.confirmPassword }}</p>
                                    </div>
                                    <br>
                                    <div class="modal_col">
                                        <h4 class="modal-title inline">Image:</h4><br>
                                        <a v-on:click="activateGalleryModal">
                                            <label class="bordered_label">
                                                <img alt="#" class="app_logo" v-bind:src="companyParamObj.logo"
                                                     v-if="companyParamObj.logo"/>
                                                <img alt="#" class="app_logo"
                                                     src="../../../../assets/images/app-image.png"
                                                     v-else/>
                                            </label>
                                        </a>
                                        <p class="error" v-if="errors.logo">{{ errors.logo }}</p>
                                        <h4 class="modal-title m_top">Actions</h4>
                                        <input type="submit" value="ADD" class="submitButton" v-on:click="submitCompany">
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="list_holder">
                    <div class="align_left mCustomScrollbar _mCS_1 mCS_no_scrollbar">
                        <div class="side_widget">
                            <div>
                                <strong class="title colored">Status:</strong>
                                <ul class="list_none side_tags">
                                    <li>
                                        <a style="cursor: pointer" class="sideBar-filter" id="active"
                                           v-on:click="fn_companyFilter('active',1)">Active
                                            <i class="fa fa-times" aria-hidden="true"
                                               v-on:click.stop="removeActive('active')"></i>

                                        </a>
                                        <a style="cursor: pointer" class="sideBar-filter" id="inactive"
                                           v-on:click="fn_companyFilter('inactive',0)">In Active
                                            <i class="fa fa-times" aria-hidden="true"
                                               v-on:click.stop="removeActive('inactive')"></i>

                                        </a>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    <div class="table_holder mCustomScrollbar">
                        <div id="example_wrapper" class="dataTables_wrapper no-footer">
                            <listing v-bind:tableColumn="dataTable.column"
                                     v-bind:tableFilterable="dataTable.filterable"
                                     v-bind:tableSortable="dataTable.sortable"
                                     v-bind:tableHeadings="dataTable.headings"
                                     v-bind:tableColumnsClasses="dataTable.columnsClasses"
                                     v-bind:apiPayLoad="dataTable.payLoad"
                                     v-bind:tableMapColumns="dataTable.mapColumns"
                                     v-bind:tableSearch="dataTable.searchProp"
                                     v-bind:tableSideBarSearch="dataTable.lookupProp"
                                     v-bind:tableReloading="dataTable.reloadProp">
                                <template slot="listing" slot-scope="data">
                                    <i class="fas fa-cog" v-on:click="segmentAction(data.actionlisting)"></i>
                                    <i class="fas fa-sort-down"></i>
                                    <ul class="table_drop list_none" style="width: 135px;" v-bind:id="data.actionlisting.row.id+'_action'">
                                        <li>
                                            <router-link
                                                    v-bind:to="'/dashboard/company-edit/' + data.actionlisting.row.id"
                                                    data-toggle="modal"
                                                    data-target="#add_lookup"><i
                                                    class="fas fa-edit"></i> Edit
                                            </router-link>
                                        </li>
                                        <li v-if="data.actionlisting.row.status">
                                            <a v-on:click="deleteCompany(data.actionlisting.row.id,0)"><i
                                                    class="fas fa-trash"></i> Inactive </a>
                                        </li>
                                        <li v-else>
                                            <a v-on:click="deleteCompany(data.actionlisting.row.id,1)"><i
                                                    class="fas fa-trash"></i> Active </a>
                                        </li>
                                        <li v-if="!data.actionlisting.row.status">
                                            <a v-on:click="removeCompany(data.actionlisting.row.id,1)"><i
                                                    class="fas fa-trash"></i> Remove </a>
                                        </li>
                                        <li v-if="data.actionlisting.row.cache_status === 'completed'">
                                            <a v-on:click="rebuildCache(data.actionlisting.row.id)"><i
                                                    class="fas fa-refresh"></i> Rebuild Cache </a>
                                        </li>
                                    </ul>
                                </template>
                                <template slot="logo" slot-scope="data">
                                    <img v-bind:src="data.imagelisting.row.logo" style="max-width: 70px;"/>
                                </template>
                            </listing>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import listing from '../../../otherComponents/datatable/datatableComponent';

    export default {
        name: "LookupListing",
        components: {
            listing,
        },
        data() {
            return {
                dataTable: {
                    reloadProp: false,
                    column: ['id', 'logo', 'name', 'email', 'roleName', 'app_user_count', 'is_active', 'created_at', 'Action'],
                    mapColumns: ['id', 'logo', 'name', 'email', 'roleName', 'app_user_count', 'is_active', 'created_at', 'status', 'Action', 'cache_status'],
                    filterable: ['id', 'logo', 'name', 'email', 'roleName', 'app_user_count', 'is_active', 'created_at', 'Action'],
                    sortable: ['id', 'logo', 'name', 'email', 'roleName', 'app_user_count', 'is_active', 'created_at'],
                    headings: {
                        id: 'Sr#',
                        logo: 'Logo',
                        name: 'Name',
                        email: 'Email',
                        roleName: 'Role',
                        app_user_count: 'Users',
                        is_active: 'Status',
                        created_at: 'Date',
                        Action: 'Action'

                    },
                    columnsClasses: {
                        id: 'width_5',
                        logo: 'width_10',
                        name: 'widdth_20',
                        email: 'widdth_20',
                        roleName: 'width_10',
                        app_user_count: 'width_10',
                        is_active: 'width_10',
                        created_at: 'widdth_20',
                        Action: 'width_10'
                    },
                    payLoad: {
                        resource: "company",
                        action: "get",
                    },
                    searchProp: '',
                    lookupProp: {},

                },
                lookupobj: {
                    search: '',
                    lookupFilter: []
                },
                companyParamObj: {
                    companyid: '0',
                    name: '',
                    email: '',
                    password: '',
                    confirmPassword: '',
                    logo: ''

                },
                errors: {
                    name: '',
                    email: '',
                    password: '',
                    confirmPassword: '',
                    logo: ''
                }


            }

        },
        mounted() {
            // this.fetchLookupFilters();
            this.listenForCustomEvents();
        },
        methods: {
            companySearch() {
                console.log('call');
                this.dataTable.searchProp = this.lookupobj.search;
            },
            segmentAction(rowObj) {
                $(".table_drop:not(#" + rowObj.row.id + "_action)").hide();
                $("#" + rowObj.row.id + "_action").slideToggle('100');
            },
            removeActive(id) {
                console.log('remove', id);
                $("#" + id).removeClass("active");
                this.dataTable.lookupProp = {};
            },
            fn_companyFilter(row, status) {
                console.log('row', row);
                console.log('status', status);
                $(".sideBar-filter").removeClass("active");
                $("#" + row).addClass("active");
                this.dataTable.lookupProp = status;
            },
            submitCompany() {
                var payload = {};
                var name = this.companyParamObj.name;
                var companyid = this.companyParamObj.companyid;
                var email = this.companyParamObj.email;
                var password = this.companyParamObj.password;
                var confirmpassword = this.companyParamObj.confirmPassword;
                var logoPath = this.companyParamObj.logo;
                if (name == '') {
                    this.errors.name = 'Please Enter Name';
                }
                if (email == '') {
                    this.errors.email = 'Please Enter Email';

                }
                if (password == '') {
                    this.errors.password = 'Please Enter Password';
                }
                if (confirmpassword == '') {
                    this.errors.confirmPassword = 'Please Enter Confirm Password';
                }
                if (name != "" && email != "" && password != "" && confirmpassword != "") {
                    this.api("post",
                        this.constants.getUrl("companySignUp"),
                        this.companyParamObj,
                        this.authHeaders
                    ).then((response) => {
                        if (response.data.status == 200) {
                            $('#add_company').modal('hide');
                            if (companyid == 0) {
                                this.$swal({
                                    position: 'top-end',
                                    type: 'success',
                                    title: 'Company has been saved',
                                    showConfirmButton: false,
                                    timer: 1500
                                })
                            } else {
                                this.$swal({
                                    position: 'top-end',
                                    type: 'success',
                                    title: 'Company has been updated',
                                    showConfirmButton: false,
                                    timer: 1500
                                })
                            }
                        } else {
                            this.errors.email = response.data.message;
                        }
                        console.log('response', response);
                        this.dataTable.reloadProp = !this.dataTable.reloadProp;
                    }).catch((error) => {
                        if (error.response.status == 422) {
                            this.errors.code = error.response.data.error.error;
                        }
                    })
                } else {
                    // alert('please add value');
                }
            },
            deleteCompany(id, status) {
                var data = {
                    'id': id,
                    'is_active': status
                };
                console.log('id', id);
                console.log('status', status);

                this.$swal({
                    title: "Delete this order status?",
                    text: "Are you sure? You won't be able to revert this!",
                    type: "warning",
                    showCancelButton: true,
                    confirmButtonColor: "#3085d6",
                    confirmButtonText: "Yes, Delete it!"
                }).then((result) => { // <--
                    console.log('result', result);
                    // alert(result);
                    if (result.value) {
                        this.api("post",
                            this.constants.getUrl("backendApiUrl"),
                            {
                                resource: "companyStatus",
                                action: "create",
                                data: data
                            },
                            this.authHeaders
                        ).then((response) => {
                            console.log('deleteResponse', response);
                            this.$swal('Deleted', 'You successfully deleted this file', 'success');
                            this.dataTable.reloadProp = !this.dataTable.reloadProp;
                        }).catch((error) => {
                            console.log('error', error);
                        });
                    }
                });
            },
            addCompany() {
                this.companyParamObj.name = '';
                this.companyParamObj.companyid = '0';
                this.companyParamObj.email = '';
                this.companyParamObj.password = '';
                this.companyParamObj.confirmPassword = '';
                this.companyParamObj.logo = '';
                this.errors.name = '';
                this.errors.companyid = '';
                this.errors.email = '';
                this.errors.password = '';
                this.errors.confirmPassword = '';
                this.errors.logo = '';
                $("#add_company").modal("show");
            },
            // handleFileUpload() {
            //     $(".submitButton").attr("disabled", true);
            //     console.log('imagepath', this.$route);
            //     console.log('file', this.$refs.file.files[0]);
            //     let formData = new FormData();
            //     formData.append('file', this.$refs.file.files[0]);
            //     console.log('data', formData);
            //     this.api("post",
            //         this.constants.getUrl("fileuploadUrl"),
            //         formData,
            //         {
            //             headers: {
            //                 'Content-Type': 'multipart/form-data'
            //             }
            //         }
            //     ).then((response) => {
            //         if (response.data.status == 200) {
            //             this.$swal({
            //                 position: 'top-end',
            //                 type: 'success',
            //                 title: response.data.message,
            //                 showConfirmButton: false,
            //                 timer: 1500
            //             });
            //             $(".submitButton").attr("disabled", true);
            //             this.companyParamObj.logo = response.data.imagePath;
            //         } else {
            //             this.$swal({
            //                 position: 'top-end',
            //                 type: 'error',
            //                 title: response.data.message,
            //                 showConfirmButton: false,
            //                 timer: 1500
            //             });
            //         }
            //         console.log('response', response);
            //     }).catch((error) => {
            //         console.log('error', error);
            //     })
            //
            // },
            rebuildCache(id) {
                this.$swal({
                    title: "Rebuild Cache?",
                    text: "Are you sure? You won't be able to revert this!",
                    type: "warning",
                    showCancelButton: true,
                    confirmButtonColor: "#3085d6",
                    confirmButtonText: "Yes, Do it!"
                }).then((result) => {
                    if (result.value) {
                        var payload = {
                            resource: "company/rebuild/cache",
                            action: "post",
                            data: {id: id}
                        };
                        this.api("post",
                            this.constants.getUrl("backendApiUrl"),
                            payload,
                            this.authHeaders
                        ).then((response) => {
                            if(response.data.status == 400 || response.data.status == 422) {
                                this.$swal('Failed', response.data.message, 'failed');
                                return
                            }
                            this.$swal('Success', 'Rebuild cache successfully.', 'success');
                            this.dataTable.reloadProp = true
                        }).catch((error) => {
                            console.log(error.response.data)
                            this.$swal('Failed', error.response.data, 'failed');
                        });
                    }
                });
            },
            removeCompany(id) {
                this.$swal({
                    title: "Delete this company?",
                    text: "Are you sure? You won't be able to revert this!",
                    type: "warning",
                    showCancelButton: true,
                    confirmButtonColor: "#3085d6",
                    confirmButtonText: "Yes, Delete it!"
                }).then((result) => { // <--
                    console.log('result', result);
                    // alert(result);
                    if (result.value) {
                        var payload = {
                            resource: "company/"+id,
                            action: "delete",
                            data: {id: id}
                        };
                        this.api("post",
                            this.constants.getUrl("backendApiUrl"),
                            payload,
                            this.authHeaders
                        ).then((response) => {
                            if(response.data.status == 400 || response.data.status == 422) {
                                this.$swal('Failed', response.data.message, 'failed');
                                return
                            }
                            this.$swal('Success', 'Company removed successfully.', 'success');
                            setTimeout(function() {
                                window.location.reload();
                            }, 1000);
                        }).catch((error) => {
                            console.log(error.response.data)
                            this.$swal('Failed', error.response.data, 'failed');
                        });
                    }
                });
            },
            activateGalleryModal() {
                this.companyParamObj.logo = '';
                this.bus.$emit('galleryModalOpen', false);
            },
            listenForCustomEvents() {
                this.bus.$on("galleryModalReturn", (url) => {
                    this.companyParamObj.logo = url;
                });
            }
        }
    }
</script>

<style>

</style>


