export default {
    //baseUrl: "http://harmeesv3.auth.test",
    //backendUrl: "http://harmeesv3.backend.test",
    baseUrl: "http://127.0.0.1:8002",
    backendUrl: "http://127.0.0.1:8000",
    login: "/api/v1/login",
    backendApiUrl: "/api/v1",
    fileuploadUrl: "/api/v1/language",
    companySignUp: "/api/v1/signup",
    logOut: "/api/v1/logout",
    getUrl: function (route) {
        return this.baseUrl + this[route];
    },
    importBackendUrl() {
        return this.backendUrl + "/download/sample-file"
    }
};


